import Vue from 'vue'
import App from './App.vue'
import _ from 'lodash'
import vuescroll from 'vuescroll';
import 'vuescroll/dist/vuescroll.css';
import { Radio, Checkbox, RadioGroup, Button, RadioButton, CheckboxGroup, Loading} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '@/assets/flexible.js'



Vue.use(vuescroll);
Vue.use(Radio);
Vue.use(Loading);
Vue.use(Checkbox);
Vue.use(RadioGroup);
Vue.use(RadioButton);
Vue.use(Button);
Vue.use(CheckboxGroup);
Vue.prototype.$vuescrollConfig = {
    bar: {
        keepShow: true,
        background: "#DDE6EE"
    }
};
Vue.prototype._ = _
Vue.config.productionTip = false

new Vue({
    render: h => h(App),
}).$mount('#app')