<template>
    <div id="app">
        <div class="window_con">
            <div class="title">
                <div>学生答题进度</div>
                <el-radio-group v-model="currMode" @change="changeMode" fill="#8BADCE" text-color="#FFFFFF">
                    <el-radio-button label="选项统计"></el-radio-button>
                    <el-radio-button label="学生统计"></el-radio-button>
                </el-radio-group>
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="boardImg" v-show="showBoard">
                <div class="imgWrap"><img :src="boardUrl" /></div>
                <div class="btn_back" @click="closeBoard"><img src="@/assets/icon_back@2x.png" />返回</div>
            </div>
            <div class="optionList" v-if="currMode=='学生统计'">
                <vue-scroll>
                    <el-checkbox-group v-model="checked" v-if="quesType==='4'">
                        <div class="optionItem" v-for="(item,$index) in updatedList" :key="item.option"
                            :class="item.isCorrOption==='1'?'right':''">
                            <div class="optionTitle">
                                <div class="optionName" :class="item.isCorrOption==='1'?'right':''">
                                    <el-checkbox :label="item.option" :key="item.option"
                                        :checked="item.isCorrOption==='1'" @change="updateBar($event,$index)">
                                        {{ item.option }}：{{item.optionStuPOs.length}}人
                                    </el-checkbox>
                                </div>
                            </div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in item.optionStuPOs" :key="stu.id">
                                    <div class="avatar"><img :src="stu.imgUrl" /></div>
                                    <div class="name">{{stu.stuName}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="optionItem" v-for="(item,$index) in answerInfo" :key="item.option"
                            v-if="!item.calculate">
                            <div class="optionTitle">
                                <div class="optionName">
                                    <el-checkbox :label="item.option" :key="item.option" disabled>
                                        {{ item.option }}：{{item.optionStuPOs.length}}人
                                    </el-checkbox>
                                </div>
                            </div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in item.optionStuPOs" :key="stu.id">
                                    <div class="avatar"><img :src="stu.imgUrl" /></div>
                                    <div class="name">{{stu.stuName}}</div>
                                </div>
                            </div>
                        </div>
                    </el-checkbox-group>
                    <el-radio-group v-model="radio" v-else>
                        <div class="optionItem" v-for="item in updatedList" :key="item.option"
                            :class="radio===item.option?'right':''">
                            <div class="optionTitle">
                                <div class="optionName" :class="radio===item.option?'right':''">
                                    <el-radio :label="item.option" :value="item.option" @change="setRadio">
                                        {{ item.option }}：{{item.optionStuPOs.length}}人
                                    </el-radio>
                                </div>
                            </div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in item.optionStuPOs" :key="stu.id">
                                    <div class="avatar"><img :src="stu.imgUrl" /></div>
                                    <div class="name">{{stu.stuName}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="optionItem" v-for="(item,$index) in answerInfo" :key="item.option"
                            v-if="!item.calculate">
                            <div class="optionTitle">
                                <div class="optionName">
                                        <el-radio :label="item.option" :value="item.option" @change="setRadio" disabled>
                                                {{ item.option }}：{{item.optionStuPOs.length}}人
                                        </el-radio>
                                </div>
                            </div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in item.optionStuPOs" :key="stu.id">
                                    <div class="avatar"><img :src="stu.imgUrl" /></div>
                                    <div class="name">{{stu.stuName}}</div>
                                </div>
                            </div>
                        </div>
                    </el-radio-group>
                </vue-scroll>
            </div>
            <div class="optionCharts" v-show="currMode=='选项统计'"
                :style="{height: this.winHeight*0.7 +'px',width: this.winWidth + 'px'}">
                <v-chart :options="option" ref="answerBar" />
                <div class="selectArea" v-show="!showBoard">
                    <div class="area" v-for="(item,index) in updatedList" @click="selectedAnswer(index)"></div>
                </div>
                <div class="answerTip">
                    {{ answerTip }}
                    <div class="checkBoard" @click="openBoard" v-if="isOnline">查看黑板图</div>
                </div>
            </div>

        </div>
    </div>
</template>
<script>
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/bar'
    import 'echarts/lib/component/tooltip'
    import 'echarts/lib/component/legend'


    export default {
        name: 'App',
        components: {
            'v-chart': ECharts
        },
        data() {
            return {
                dpr: 1,
                isOnline: false,
                showBoard: false,
                winHeight: null,
                answerTip: "请选择正确答案",
                quesType: -1,
                currId: 0,
                currMode: "选项统计",
                checked: [],
                radio: "",
                answerInfo: null,
                updatedList: null,
                boardUrl: "",
                option: {
                    color: ['#BAD1E5', '#00D97F'],
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        },
                        formatter: function (params) {
                            var tar;
                            if (params[1].value != '-') {
                                tar = params[1];
                            }
                            else {
                                tar = params[0];
                            }
                            return tar.name + '<br/>' + "人数" + ' : ' + tar.value;
                        }
                    },
                    legend: {
                        show: false
                    },
                    grid: {
                        top: '10%',
                        left: '9%',
                        right: '10%',
                        bottom: '9%',
                        containLabel: false
                    },
                    xAxis: {
                        nameTextStyle: {
                            color: "#96A4B2",
                            fontSize: 18 * localStorage.getItem("width") / 1920,
                        },
                        name: "选项",
                        axisLine: {
                            lineStyle: {
                                color: "#C0C8D1"
                            }
                        },

                        data: [],

                    },
                    yAxis: {
                        name: "人数",
                        splitNumber: 1,
                        nameTextStyle: {
                            color: "#96A4B2",
                            fontSize: 18 * localStorage.getItem("width") / 1920,
                        },
                        axisLine: {
                            lineStyle: {
                                color: "#C0C8D1"
                            }
                        },
                        axisLabel: {
                            color: "#96A4B2",
                            fontSize: 16 * localStorage.getItem("width") / 1920
                        },
                        splitLine: {
                            lineStyle: {
                                width: 1 * localStorage.getItem("width") / 1920,
                                color: "#C0C8D1",
                                type: 'dashed'
                            }
                        },
                    },
                    series: [
                        {
                            name: '选项',
                            type: 'bar',
                            stack: '人数',
                            barMaxWidth: "30%",
                            data: []
                        },
                        {
                            name: '正确',
                            type: 'bar',
                            stack: '人数',
                            barMaxWidth: "30%",
                            data: []
                        }
                    ]
                }
            }
        },
        computed: {
            optionList: function () {
                let arr = []
                for (let option of this.updatedList) {
                    arr.push({ value: option.option, textStyle: { fontSize: 33 * this.dpr, color: '#2E4A66' } })
                }
                return arr
            },
            rightOption: function () {
                let arr = []
                for (let option of this.updatedList) {
                    if (option.isCorrOption === '1') {
                        arr.push(option.optionStuPOs.length)
                    } else {
                        arr.push('-')
                    }

                }
                return arr
            },
            selectedOption: function () {
                let arr = []
                for (let option of this.updatedList) {
                    if (option.isCorrOption === '1') {
                        arr.push(option.option)
                    }
                }
                return arr.join("")
            },
            normalOption: function () {
                let arr = []
                for (let option of this.updatedList) {
                    if (option.isCorrOption !== '1') {
                        arr.push(option.optionStuPOs.length)
                    } else {
                        arr.push('-')
                    }

                }
                return arr
            }
        },
        mounted() {

            // alert(window.innerWidth,window.innerHeight)
            console.log(window.innerWidth, window.innerHeight)
            //获取url参数
            console.log(this.winHeight, window.innerHeight)
            console.log(this.quesType, this.currId)
            this.loadRecordInfo(this.currId)
        },
        beforeMount() {
            this.dpr = localStorage.getItem("width") / 1920
            this.quesType = utils.getUrlPrams("type")
            this.winWidth = parseInt(utils.getUrlPrams("width")) - 40
            this.winHeight = parseInt(utils.getUrlPrams("height")) - 40
            this.currId = utils.getUrlPrams("id")
            this.isOnline = utils.getUrlPrams("isOnline") == "1" ? true : false
        },
        methods: {
            closeBoard: function () {
                this.showBoard = false
            },
            openBoard: function () {
                this.showBoard = true
            },
            initTip: function () {
                if (this.selectedOption.length > 0) {
                    this.answerTip = "已选择：" + this.selectedOption
                } else {
                    this.answerTip = "请选择正确答案"
                }
            },
            initRadio: function () {
                for (let option of this.updatedList) {
                    if (option.isCorrOption === '1') {
                        this.radio = option.option
                    }
                }
            },
            setRadio(val) {
                this.radio = val
                let list = this.updatedList
                for (let i = 0; i < list.length; i++) {
                    if (list[i].option === val) {
                        this.updatedList[i].isCorrOption = "1"
                    } else {
                        this.updatedList[i].isCorrOption = "0"
                    }
                }
                this.option.series[0].data = this.normalOption
                this.option.series[1].data = this.rightOption
                this.postAnswer()
            },
            selectedAnswer(index) {
                if (this.quesType === '4') {
                    if (this.updatedList[index].isCorrOption === "1") {
                        this.updatedList[index].isCorrOption = "0"
                    } else {
                        this.updatedList[index].isCorrOption = "1"
                    }
                } else {
                    let list = this.updatedList
                    for (let i = 0; i < list.length; i++) {
                        if (list[i].option === this.updatedList[index].option) {
                            this.updatedList[i].isCorrOption = "1"
                        } else {
                            this.updatedList[i].isCorrOption = "0"
                        }
                    }
                }
                this.option.series[0].data = this.normalOption
                this.option.series[1].data = this.rightOption
                this.postAnswer()
            },
            postAnswer() {
                let right = this.selectedOption
                let id = this.currId
                let that = this
                axios.get(API.api_tl + 'api/subCorrOption', {
                    params: {
                        interactRecordId: id,
                        corrOption: right
                    }
                }).then(function (res) {
                    console.log(res)
                    that.initTip()
                }).catch(function (error) {
                    console.log(error);
                });
            },
            updateBar(val, index) {
                console.log(val, index)
                this.updatedList[index].isCorrOption = val ? "1" : "0"
                this.option.series[0].data = this.normalOption
                this.option.series[1].data = this.rightOption
                this.postAnswer()
            },
            changeMode(e) {
                console.log(e)
                this.currMode = e
                this.initRadio()
            },
            loadRecordInfo() {
                let that = this
                let id = this.currId
                axios.get(API.api_tl + 'api/selOptionStatistic', {
                    params: {
                        // taskIds: that.taskIds.join(",")
                        interactRecordId: id
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    that.answerInfo = res.data.data.optionList
                    that.updatedList = that._.filter(that.answerInfo, ['calculate', 1])
                    that.boardUrl = res.data.data.blackImg
                    that.option.xAxis.data = that.optionList
                    that.option.color = that.colorList
                    that.option.series[0].data = that.normalOption
                    that.option.series[1].data = that.rightOption
                    that.initTip()
                    if (that.quesType !== 4) {
                        that.initRadio()
                    }
                    console.log(that.option)
                }).catch(function (error) {
                    console.log(error);
                });
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowSuitang",    // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        background: transparent;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
        width: 100%;
    }

    .echarts {
        width: 100%;
        height: 100%;
    }

    .el-radio-group {
        width: 100% !important;
    }

    .el-checkbox-group {
        width: 100% !important;
    }

    .el-radio__input.is-checked .el-radio__inner {
        border-color: #49AF4F !important;
        background: #49AF4F !important;
    }

    .el-radio__inner:hover {
        border-color: #49AF4F !important;
    }

    .el-radio__input.is-checked+.el-radio__label {
        color: #2E4A66 !important;
    }

    .el-radio-button__inner {
        color: #8BADCE !important;
    }

    .el-radio-button.is-active .el-radio-button__inner {
        color: #fff !important;
    }

    .el-radio-button__inner {
        color: #8BADCE !important;
        font-size: 0.14rem !important;
        padding: 0.12rem 0.2rem !important;
    }

    .el-checkbox__inner:hover {
        border-color: #49AF4F !important;
    }

    .el-checkbox__input.is-checked .el-checkbox__inner {
        background-color: #49AF4F !important;
        border-color: #49AF4F !important;
    }

    .el-checkbox__input.is-checked+.el-checkbox__label {
        color: #2E4A66 !important;
    }

    .el-checkbox__input.is-focus .el-checkbox__inner {
        border-color: #49AF4F !important;
    }

    .el-checkbox__label,
    .el-radio__label {
        padding-left: 0.1rem !important;
        line-height: 0.19rem !important;
        font-size: 0.14rem !important;
    }

    .el-checkbox__inner,
    .el-radio__inner {
        width: 0.14rem !important;
        height: 0.14rem !important;

    }

    .el-checkbox__inner {
        border-radius: 0.02rem !important;
    }

    .el-checkbox__inner::after {
        border-width: 0.01rem !important;
        height: 0.07rem !important;
        left: 0.04rem !important;
        top: 0.01rem !important;
        width: 0.03rem !important;
    }

    .el-radio__inner::after {
        width: 0.04rem !important;
        height: 0.04rem !important;
    }
</style>
